@import '../../App.scss';

.showcase {
    width: 100%;
    overflow-x: hidden;
}

.gallery {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    padding: $v-padding-desktop $h-padding-desktop 5rem $h-padding-desktop;
}

.card__container {
    position: relative;
    height: 30vw;
    @include transition;
}

.card__container:hover {
    @include hoverEffect;
}

.card__content {
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
}

.card__info {
    padding: 2rem;
}

.card__title {
    font-size: 2rem;
    font-weight: 800;
}

.card__subtitle {
    font-size: 1.5rem;
    font-weight: 400;
    white-space: pre-line;
}

.figure-header {
    width: 100%;
    padding-right: 1rem;
}

.figure-header .detail-header__description {
    margin-block-start: 2rem;
}

.contents {
    overflow: scroll;
}

.contents__left, .contents__right {
    //https://stackoverflow.com/questions/17982111/sass-variable-in-css-calc-function
    width: calc((100vw - #{$h-padding-desktop} * 2)/2 - 2rem);
    display: inline-block;
}

.contents__left {
    float: left;
}

.contents__right {
    float: right;
    padding: $v-padding-desktop $h-padding-desktop $v-padding-desktop 0;
}

.figure__hero {
    width: 80%;
}

.figure-article {
    padding: 0 0 $v-padding-desktop $h-padding-desktop;
}

.figure-article h2 {
    font-size: 1.5rem;
    font-weight: 400;
    margin-block-end: 2rem;
}

.figure-article p {
    white-space: pre-wrap;
    line-height: 1.8em;
}

.contents__right__article-images {
    margin-block-start: 2rem;
}